<template>
	<div>
		<!-- 面包屑导航区 -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
			<el-breadcrumb-item>用户管理</el-breadcrumb-item>
			<el-breadcrumb-item>用户列表</el-breadcrumb-item>
		</el-breadcrumb>

		<!-- 卡片视图区 -->
		<el-card class="box-card">
			<!-- 这是搜索与添加区域 -->
			<el-row :gutter="20">
				<el-col :span="8">
					<el-input placeholder="请输入内容" v-model="queryInfo.query" clearable @clear="getUserList()">
						<el-button slot="append" icon="el-icon-search" @click="getUserList()"></el-button>
					</el-input>
				</el-col>
				<el-col :span="4">
					<el-button type="primary" @click="addDialogVisible=true">添加用户</el-button>
				</el-col>
			</el-row>
			<!-- 用户列表区域 -->
			<el-table :data="userlist" border stripe>
				<el-table-column type="index"></el-table-column>
				<el-table-column label="姓名" prop="username"></el-table-column>
				<el-table-column label="邮箱" prop="email"></el-table-column>
				<el-table-column label="电话" prop="mobile"></el-table-column>
				<el-table-column label="角色" prop="role_name"></el-table-column>
				<el-table-column label="状态">
					<template slot-scope="scope">
						<!-- {{scope.row}} -->
						<el-switch v-model="scope.row.mg_state" @change="userStateChanged(scope.row)">
						</el-switch>
					</template>
				</el-table-column>
				<el-table-column label="操作">
					<template slot-scope="scope">
						<!-- 修改按钮 -->
						<el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
						<el-button type="danger" icon="el-icon-delete" circle @click="removeUserById(scope.row.id)"></el-button>
						<el-tooltip effect="dark" content="分配角色" placement="top" :enterable="false">
							<el-button type="warning" icon="el-icon-setting" circle></el-button>
						</el-tooltip>
					</template>
				</el-table-column>
			</el-table>
			<!-- 分页区域 -->
			<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
				:current-page="queryInfo.pagenum" :page-sizes="[1, 2, 5, 10]" :page-size="queryInfo.pagesize"
				layout="total, sizes, prev, pager, next, jumper" :total="total">
			</el-pagination>
		</el-card>
		<!-- 添加用户的对话框 -->
		<el-dialog title="添加用户" :visible.sync="addDialogVisible" width="50%" @close = "addDialogClosed">
			<!-- 内容主体区域  form表单验证-->
			<el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
				<el-form-item label="用户名" prop="username">
					<el-input v-model="addForm.username"></el-input>
				</el-form-item>
				<el-form-item label="密码" prop="password">
					<el-input v-model="addForm.password"></el-input>
				</el-form-item>
				<el-form-item label="邮箱" prop="email">
					<el-input v-model="addForm.email"></el-input>
				</el-form-item>
				<el-form-item label="手机" prop="mobile">
					<el-input v-model="addForm.mobile"></el-input>
				</el-form-item>
			</el-form>
			<span slot="footer" class="dialog-footer">
				<el-button @click="addDialogVisible = false">取 消</el-button>
				<el-button type="primary" @click="addUser">确 定</el-button>
			</span>
		</el-dialog>
		
		<!-- 修改用户的对话框 -->
		<el-dialog title="修改用户" :visible.sync="editDialogVisible" width="50%" >
			<!-- 内容主体区域  form表单验证-->
			<el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
				<el-form-item label="用户名">
					<el-input v-model="editForm.username" disabled></el-input>
				</el-form-item>
				<el-form-item label="邮箱" prop="email">
					<el-input v-model="editForm.email"></el-input>
				</el-form-item>
				<el-form-item label="手机" prop="mobile">
					<el-input v-model="editForm.mobile"></el-input>
				</el-form-item>
			</el-form>
			<span slot="footer" class="dialog-footer">
				<el-button @click="editDialogVisible = false">取 消</el-button>
				<el-button type="primary" @click="editUserInfo">确 定</el-button>
			</span>
		</el-dialog>
		
	</div>
</template>

<script>
	export default {
		data() {
				// 验证邮箱的规则
			var checkEmail = ( rule,value,cb) => {
				const regEamil = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
				if( regEamil.test(value) ){
					return cb()
				}
				cb( new Error('请输入合法的邮箱'))
			}
			// 验证手机号的规则
			var checkMobile = (rule,value,cb) => {
				const regMobile = /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/
				if(regMobile.test(value)) {
					return cb()
				}
				cb( new Error('请输入合法的手机号'))
			}
			return {
				queryInfo: {
					query: '',
					pagenum: 1,
					pagesize: 5
				},
				userlist: [],
				total: 0,
				// 控制添加用户对话框的显示与隐藏
				addDialogVisible: false,
				// 添加用户表单的数据
				addForm: {
					username: '',
					password: '',
					email: '',
					mobile: ''
				},
				// 添加表单的验证规则对象
				addFormRules: {
					username: [{
							required: true,
							message: "请输入用户名",
							trigger: 'blur'
						},
						{
							min: 3,
							max: 10,
							message: '用户名的长度在 3~10 个字符之间',
							trigger: 'blur'
						}
					],
					password:[{
							required: true,
							message: "请输入用户密码",
							trigger: 'blur'
						},
						{
							min: 6,
							max: 15,
							message: '用户密码的长度在 6~15 个字符之间',
							trigger: 'blur'
						}
					],
					email: [{
							required: true,
							message: "请输入用户邮箱",
							trigger: 'blur'
						},
						{
							min: 6,
							max: 35,
							message: '用户邮箱的长度在 6~35 个字符之间',
							trigger: 'blur'
						},
						{ validator: checkEmail, trigger: 'blur' }
					],
					mobile: [
						{
							required: true,
							message: "请输入用户手机号码",
							trigger: 'blur'
						},
						{
							min: 6,
							max: 15,
							message: '用户手机号的长度为 11 个字符',
							trigger: 'blur'
						},
						{ validator: checkMobile, trigger: 'blur' }
						
					]
				},
				// 控制修改用户对话框的显示与隐藏
				editDialogVisible: false,
				editForm: {
					username: '',
					password: '',
					email: '',
					mobile: ''
				},
				// 修改表单的验证规则对象
				editFormRules: {
					email: [
						{ required: true, message: "请输入用户邮箱",	trigger: 'blur'	},
						{ validator: checkEmail, trigger: 'blur' }
					],
					mobile: [
						{required: true,message: "请输入用户手机号码",trigger: 'blur'	},
						{ validator: checkMobile, trigger: 'blur' }
					]
				}
			} //retrun end 
		},

		created() {
			this.getUserList()
		},
		methods: {
			async getUserList() {
				const {
					data: res
				} = await this.$http.get('users', {
					params: this.queryInfo
				})
				if (res.meta.status !== 200) {
					return this.$message.error('获取用户列表失败！')
				}
				this.userlist = res.data.users
				this.total = res.data.total
				// console.log(this.total)
			},
			// 监听 pagesize 改变的事件
			handleSizeChange(newSize) {
				this.queryInfo.pagesize = newSize
				this.getUserList()
			},
			// 监听 页码值 改变的事件
			handleCurrentChange(newPage) {
				this.queryInfo.pagenum = newPage
				this.getUserList()
			},
			// 监听 switch 开关状态的变化
			async userStateChanged(userinfo) {
				// console.log(userinfo)
				const {
					data: res
				} = await this.$http.put(`users/${userinfo.id}/state/${userinfo.mg_state}`)
				if (res.meta.status !== 200) {
					userinfo.mg_state = !userinfo.mg_state
					return this.$message.error("更新用户状态失败")
				}
				this.$message.success("更新用户状态成功！")
			},
			// 监听添中用户对话框的关闭事件
			// 产生全新的表单
			addDialogClosed() {
				this.$refs.addFormRef.resetFields()
				
			},
			addUser() {
				this.$refs.addFormRef.validate(async valid => {
					if(!valid) return
					// console.log(this.addForm)
					const { data: res } = await  this.$http.post('users',this.addForm)
					if(res.meta.status !== 201 ){
						this.$message.error('添加用户失败!')
					}
						this.$message.success('添加用户成功!')
						this.addDialogVisible = false
						this.getUserList()
				})
			},
			async showEditDialog(id) {
				// console.log(id)
				const { data: res } = await this.$http.get('users/' + id)
				if( res.meta.status != 200 ) {  return this.$message.error('查询用户信息失败!') }  
				this.editForm = res.data
				this.editDialogVisible = true
			},
			// 修改用户信息并提交
			editUserInfo() {
				this.$refs.editFormRef.validate(async valid => {
					// console.log(valid)
					if(!valid) return
					const { data: res } = await this.$http.put('users/' + this.editForm.id,{
						email: this.editForm.email,
						mobile:this.editForm.mobile
						})
					if( res.meta.status != 200) return this.$message.error('更新用户信息失败!')
					
					// 关闭对话框
					this.editDialogVisible = false
					// 刷新数据
					this.getUserList()
					// 提示修改成功
					this.$message.success('用户信息修改成功！')
				})
			},
			// 根据 Id 删除对应的用户
			async removeUserById(id) {
				console.log(id)
				// 弹框询问用户是否删除数据
				const confirmResult =  await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
				          confirmButtonText: '确定',
				          cancelButtonText: '取消',
				          type: 'warning'
				        }).catch(err => err)
				// 返回confirm 或 cancel
				// console.log(res)
				if(confirmResult !== 'confirm' ) {
					return this.$message.info('已取消删除')
				}
				console.log('确认了删除')
				
				
				
				
				const { data: res }  = await this.$http.delete('users/' + id)
				// console.log(res)
				if( res.meta.status != 200) return this.$message.error('删除用户失败!')
				this.getUserList()
				// 提示修改成功
				this.$message.success('删除用户成功！')
			}
		}
	}
</script>

<style lang="less" scoped>
	.el-breadcrumb {
		margin-bottom: 15px;
	}

	.el-card {
		box-shadow: 0 5px 5px rgba(0, 0, 0, 0.15) !important;
	}

	.el-pagination {
		margin-top: 15px;
	}
</style>
